import { useRef } from 'react';
import type { Client, WorkItem } from '../../types/domain';
import { useCalendar } from '../../hooks/useCalendar';
import { useNumberFormat } from '../../hooks/useNumberFormat';
import { useClientWorkplaces } from '../../hooks/useClientWorkplaces';
import { useProjects } from '../../hooks/useProjects';
type BulkItem = Partial<WorkItem> & {
  status?: string;
};
type Props = {
  open: boolean;
  clients: Client[];
  categories: string[];
  units: string[];
  bulkItems: BulkItem[];
  bulkBaseInfo: {
    clientId: string | number;
    workplaceId: string | number;
    projectName: string;
    date?: string;
    bulkLaborPersons?: string | number;
    bulkLaborUnitRate?: string | number;
  };
  showBulkCustomProject: boolean;
  statuses: string[];
  onBaseInfoChangeField: (name: string, value: string) => void;
  onItemChange: (index: number, field: string, value: string) => void;
  onAddItem: () => void;
  onRemoveItem: (index: number) => void;
  onCancel: () => void;
  onSubmit: (e: React.FormEvent<HTMLFormElement>) => void;
};
export default function BulkFormModal({
  open,
  clients,
  categories,
  units,
  bulkItems,
  bulkBaseInfo,
  showBulkCustomProject,
  statuses,
  onBaseInfoChangeField,
  onItemChange,
  onAddItem,
  onRemoveItem,
  onCancel,
  onSubmit
}: Props) {
  const {
    getClientWorkplaces
  } = useClientWorkplaces();
  const {
    getClientProjects
  } = useProjects();
  const {
    format
  } = useNumberFormat();
  const cal = useCalendar({
    value: bulkBaseInfo?.date ?? '',
    onChange: d => onBaseInfoChangeField('date', d)
  });
  const calRef = useRef<HTMLDivElement>(null);
  if (open !== true) return null;
  return <div className="fixed inset-0 bg-gray-800/50 dark:bg-gray-900/70 backdrop-blur-sm overflow-y-auto h-full w-full z-50">
      <div className="relative top-6 mx-auto w-[1100px] max-w-[96vw] shadow-2xl rounded-2xl bg-white/80 dark:bg-gray-800/90 ring-1 ring-black/5 dark:ring-white/10 mb-8">
        <div className="rounded-t-2xl bg-gradient-to-br from-indigo-50 via-purple-50 to-white dark:from-indigo-900/30 dark:via-purple-900/30 dark:to-gray-800 px-8 pt-8 pb-6 text-center">
          <h3 className="text-2xl font-extrabold tracking-tight text-indigo-600 dark:text-indigo-400">   </h3>
          <p className="mt-2 text-sm text-gray-500 dark:text-gray-400">    </p>
        </div>
        <div className="px-6 pb-6">
          <form id="bulk-work-item-form" onSubmit={onSubmit} className="space-y-4">
            <div className="bg-white dark:bg-gray-800 rounded-xl shadow-sm border border-gray-100 dark:border-gray-700 p-4">
              <div className="flex items-center mb-3 gap-3">
                <span className="inline-flex h-7 w-7 items-center justify-center rounded-full bg-indigo-50 dark:bg-indigo-900/30 text-indigo-600 dark:text-indigo-400"></span>
                <h4 className="text-base font-semibold text-gray-900 dark:text-gray-100"> </h4>
              </div>
              <div className="grid grid-cols-4 gap-3 mb-3">
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"></label>
                  <select name="clientId" value={String(bulkBaseInfo.clientId ?? '')} onChange={e => onBaseInfoChangeField(e.target.name, e.target.value)} className="mt-1 block w-full border border-gray-200 dark:border-gray-600 rounded-md px-3 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500" required>
                    <option value=""> </option>
                    {clients.map(client => <option key={client.id} value={String(client.id)}>{client.name}</option>)}
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"></label>
                  {(() => {
                  const hasClient = String(bulkBaseInfo.clientId ?? '') !== '';
                  return <select name="workplaceId" value={String(bulkBaseInfo.workplaceId ?? '')} onChange={e => onBaseInfoChangeField(e.target.name, e.target.value)} className="mt-1 block w-full border border-gray-200 dark:border-gray-600 rounded-md px-3 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500" required disabled={!hasClient}>
                        <option value="">{hasClient ? ' ' : ' (  )'}</option>
                        {hasClient && getClientWorkplaces(bulkBaseInfo.clientId).map(workplace => <option key={workplace.id} value={String(workplace.id)}>{workplace.name} - {workplace.address}</option>)}
                      </select>;
                })()}
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"></label>
                  {showBulkCustomProject !== true ? (() => {
                  const hasClient = String(bulkBaseInfo.clientId ?? '') !== '';
                  return <select name="projectName" value={bulkBaseInfo.projectName} onChange={e => onBaseInfoChangeField(e.target.name, e.target.value)} className="mt-1 block w-full border border-gray-200 dark:border-gray-600 rounded-md px-3 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500" required disabled={!hasClient}>
                          <option value=""> </option>
                          {hasClient && getClientProjects(bulkBaseInfo.clientId).map(project => <option key={project} value={project}>{project}</option>)}
                          <option value="custom">+   </option>
                        </select>;
                })() : <div className="flex space-x-2">
                      <input type="text" name="projectName" value={bulkBaseInfo.projectName} onChange={e => onBaseInfoChangeField(e.target.name, e.target.value)} placeholder="  " className="mt-1 block w-full border border-gray-200 dark:border-gray-600 rounded-md px-3 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500" required />
                      <button type="button" onClick={() => onBaseInfoChangeField('projectName', '')} className="mt-1 px-2 py-2 text-gray-400 hover:text-gray-600" title="   "></button>
                    </div>}
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"></label>
                  <div className="mt-1 relative inline-block" ref={calRef}>
                    <div className="flex items-center gap-2">
                      <input type="text" name="date" value={bulkBaseInfo?.date ?? ''} onChange={e => onBaseInfoChangeField(e.target.name, e.target.value)} placeholder="YYYY-MM-DD" inputMode="numeric" className="block w-full border border-gray-200 rounded-md px-3 py-2 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 placeholder-gray-400" onFocus={() => cal.setOpen(true)} required />
                      <button type="button" className="px-2 py-2 text-gray-600 hover:text-gray-800" onClick={() => cal.setOpen(v => !v)} title=" "></button>
                    </div>
                    {cal.open && <div className="absolute z-50 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-md shadow-lg mt-2 p-3" style={{
                    transform: 'scale(1)',
                    transformOrigin: 'top left'
                  }}>
                        <div className="flex items-center justify-between mb-2">
                          <button type="button" className="px-2 py-1 text-sm border dark:border-gray-600 rounded text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700" onClick={cal.prevMonth}></button>
                          <div className="text-sm font-medium text-gray-900 dark:text-gray-100">{cal.month.getFullYear()} {cal.month.getMonth() + 1}</div>
                          <button type="button" className="px-2 py-1 text-sm border dark:border-gray-600 rounded text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700" onClick={cal.nextMonth}></button>
                        </div>
                        <table className="text-xs select-none">
                          <thead>
                            <tr className="text-left text-gray-600 dark:text-gray-400">{['', '', '', '', '', '', ''].map((d, idx) => {
                            const SUN = 0; // eslint-disable-line no-magic-numbers
                            const SAT = 6; // eslint-disable-line no-magic-numbers
                            return <th key={d} className={`px-2 py-1 ${idx === SUN ? 'text-red-600' : idx === SAT ? 'text-blue-600' : ''}`}>{d}</th>;
                          })}</tr>
                          </thead>
                          <tbody>{cal.getCalendarGrid().map((row, idx) => <tr key={idx} className="text-left">{row.map((d, i2) => {
                            const clickable = typeof d === 'number' && d !== 0 && !Number.isNaN(d);
                            const SUN = 0; // eslint-disable-line no-magic-numbers
                            const SAT = 6; // eslint-disable-line no-magic-numbers
                            const dayColor = i2 === SUN ? 'text-red-600' : i2 === SAT ? 'text-blue-600' : '';
                            return <td key={i2} className={`px-2 py-1 ${dayColor} ${clickable ? 'cursor-pointer hover:bg-gray-100 rounded' : ''}`} onClick={() => {
                              if (clickable) cal.pickDate(d as number);
                            }}>
                                  {clickable ? d : ''}
                                </td>;
                          })}</tr>)}</tbody>
                        </table>
                      </div>}
                  </div>
                </div>
              </div>

              <div className="border-t border-gray-200 dark:border-gray-700 pt-3">
                <div className="grid grid-cols-3 gap-3">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"> </label>
                    <input type="text" name="bulkLaborPersons" value={bulkBaseInfo.bulkLaborPersons ?? ''} onChange={e => onBaseInfoChangeField(e.target.name, e.target.value)} onFocus={e => e.target.select()} placeholder=": 3" className="mt-1 block w-full border border-gray-200 dark:border-gray-600 rounded-md px-3 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500" />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"> </label>
                    <input type="text" name="bulkLaborUnitRate" value={(() => {
                    const val = bulkBaseInfo.bulkLaborUnitRate;
                    if (val === null || val === undefined || val === '') return '';
                    const num = Number(val);
                    return Number.isFinite(num) ? format(num) : String(val);
                  })()} onChange={e => onBaseInfoChangeField(e.target.name, e.target.value)} onFocus={e => e.target.select()} placeholder=": 250,000" className="mt-1 block w-full border border-gray-200 dark:border-gray-600 rounded-md px-3 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500" />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"> </label>
                    <div className="mt-1 w-full bg-indigo-50 dark:bg-indigo-900/30 border border-indigo-200 dark:border-indigo-700 rounded px-3 py-2 text-sm font-semibold text-indigo-900 dark:text-indigo-300">{(() => {
                      const persons = Number(bulkBaseInfo.bulkLaborPersons);
                      const rate = Number(bulkBaseInfo.bulkLaborUnitRate);
                      const p = Number.isFinite(persons) ? persons : 0;
                      const r = Number.isFinite(rate) ? rate : 0;
                      return `${format(p * r)}`;
                    })()}</div>
                  </div>
                </div>
              </div>
            </div>

            <div className="bg-white dark:bg-gray-800 rounded-xl shadow-sm border border-gray-100 dark:border-gray-700 p-4">
              <div className="flex justify-between items-center mb-2">
                <div className="flex items-center gap-3">
                  <span className="inline-flex h-7 w-7 items-center justify-center rounded-full bg-green-50 dark:bg-green-900/30 text-green-600 dark:text-green-400"></span>
                  <h4 className="text-base font-semibold text-gray-900 dark:text-gray-100"> </h4>
                </div>
                <button type="button" onClick={onAddItem} className="text-indigo-600 dark:text-indigo-400 hover:text-indigo-800 dark:hover:text-indigo-300 text-sm">+  </button>
              </div>

              <div className="space-y-3 max-h-96 overflow-y-auto">
                {bulkItems.map((item, index) => <div key={index} className="border border-gray-200 dark:border-gray-600 rounded-lg p-4 bg-gray-50 dark:bg-gray-700/50">
                    <div className="flex justify-between items-center mb-3">
                      <span className="text-sm font-medium text-gray-700 dark:text-gray-300">  #{index + 1}</span>
                      {bulkItems.length > 1 && <button type="button" onClick={() => onRemoveItem(index)} className="text-red-600 dark:text-red-400 hover:text-red-800 dark:hover:text-red-300 text-sm"></button>}
                    </div>

                    {/* 1: , ,  */}
                    <div className="grid grid-cols-3 gap-3 mb-2">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"></label>
                        <input type="text" value={item.name ?? ''} onChange={e => onItemChange(index, 'name', e.target.value)} className="mt-1 block w-full border border-gray-200 dark:border-gray-600 rounded-md px-3 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500" required />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"></label>
                        <select value={item.category ?? ''} onChange={e => onItemChange(index, 'category', e.target.value)} className="mt-1 block w-full border border-gray-200 dark:border-gray-600 rounded-md px-3 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500" required>
                          <option value=""> </option>
                          {categories.map(cat => <option key={cat} value={cat}>{cat}</option>)}
                        </select>
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"> </label>
                        <input type="text" value={item.description ?? ''} onChange={e => onItemChange(index, 'description', e.target.value)} className="mt-1 block w-full border border-gray-200 dark:border-gray-600 rounded-md px-3 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500" />
                      </div>
                    </div>

                    {/* 2: , , ,  */}
                    <div className="grid grid-cols-4 gap-3 mb-2">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"></label>
                        <input type="text" value={item.quantity ?? ''} onChange={e => onItemChange(index, 'quantity', e.target.value)} onFocus={e => e.target.select()} placeholder=": 1" className="mt-1 block w-full border border-gray-200 dark:border-gray-600 rounded-md px-3 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500" />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"></label>
                        <select value={item.unit ?? ''} onChange={e => onItemChange(index, 'unit', e.target.value)} className="mt-1 block w-full border border-gray-200 dark:border-gray-600 rounded-md px-3 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500">
                          <option value=""> </option>
                          {units.map(u => <option key={u} value={u}>{u}</option>)}
                        </select>
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"></label>
                        <input type="text" value={(() => {
                      const val = item.defaultPrice;
                      if (val === null || val === undefined) return '';
                      const num = Number(val);
                      return Number.isFinite(num) ? format(num) : String(val);
                    })()} onChange={e => onItemChange(index, 'defaultPrice', e.target.value)} onFocus={e => e.target.select()} placeholder=": 200,000" className="mt-1 block w-full border border-gray-200 dark:border-gray-600 rounded-md px-3 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500" />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"></label>
                        <div className="mt-1 w-full bg-green-50 dark:bg-green-900/30 border border-green-200 dark:border-green-700 rounded px-3 py-2 text-sm font-semibold text-green-900 dark:text-green-300">{(() => {
                        const pRaw = item.defaultPrice;
                        const qRaw = item.quantity;
                        const pNum = Number(pRaw);
                        const qNum = Number(qRaw);
                        const p = Number.isFinite(pNum) ? pNum : 0;
                        const q = Number.isFinite(qNum) ? qNum : 1;
                        const total = p * q;
                        return `${format(total)}`;
                      })()}</div>
                      </div>
                    </div>

                    {/* 3:  */}
                    <div className="mb-3">
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300"></label>
                      <input type="text" value={item.notes ?? ''} onChange={e => onItemChange(index, 'notes', e.target.value)} className="mt-1 block w-full border border-gray-200 dark:border-gray-600 rounded-md px-3 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-gray-100 placeholder-gray-400 dark:placeholder-gray-500 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500" />
                    </div>

                    {/*    */}
                    <div className="flex justify-between items-center">
                      <div className="flex-1">
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1"></label>
                        <div className="flex flex-wrap gap-1.5">
                          {statuses.map(s => {
                        const active = item.status === s;
                        const classes = active ? 'bg-indigo-600 dark:bg-indigo-500 text-white border-transparent shadow' : 'bg-gray-100 dark:bg-gray-600 text-gray-700 dark:text-gray-300 border border-gray-200 dark:border-gray-500 hover:bg-gray-200 dark:hover:bg-gray-500';
                        return <button key={s} type="button" onClick={() => onItemChange(index, 'status', s)} className={`px-2.5 py-1 rounded-full text-xs transition ${classes}`} aria-pressed={active}>{s}</button>;
                      })}
                        </div>
                      </div>
                      <div className="ml-4">
                        <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1 text-right"> </label>
                        <div className="bg-blue-600 dark:bg-blue-700 text-white rounded-lg px-4 py-2 text-base font-bold shadow-md">{(() => {
                        const pRaw = item.defaultPrice;
                        const qRaw = item.quantity;
                        const pNum = Number(pRaw);
                        const qNum = Number(qRaw);
                        const p = Number.isFinite(pNum) ? pNum : 0;
                        const q = Number.isFinite(qNum) ? qNum : 1;
                        const laborPersons = Number(bulkBaseInfo.bulkLaborPersons);
                        const laborRate = Number(bulkBaseInfo.bulkLaborUnitRate);
                        const lp = Number.isFinite(laborPersons) ? laborPersons : 0;
                        const lr = Number.isFinite(laborRate) ? laborRate : 0;
                        const laborCost = lp * lr;
                        const total = p * q + laborCost;
                        return `${format(total)}`;
                      })()}</div>
                      </div>
                    </div>
                  </div>)}
              </div>
            </div>
          </form>
        </div>

        {/* Sticky   */}
        <div className="sticky bottom-0 left-0 right-0 bg-white/95 dark:bg-gray-800/95 backdrop-blur-sm border-t border-gray-200 dark:border-gray-700 px-6 py-4 rounded-b-2xl shadow-lg">
          <div className="flex justify-end space-x-3">
            <button type="button" onClick={onCancel} className="px-6 py-2.5 text-gray-700 dark:text-gray-300 bg-gray-100 dark:bg-gray-700 hover:bg-gray-200 dark:hover:bg-gray-600 rounded-lg transition font-medium">
              
            </button>
            <button type="submit" form="bulk-work-item-form" className="px-6 py-2.5 rounded-lg bg-gradient-to-r from-indigo-500 to-purple-600 hover:from-indigo-600 hover:to-purple-700 text-white shadow-md transition font-bold">
              {bulkItems.length}   
            </button>
          </div>
        </div>
      </div>
    </div>;
}